// declare in order
const deviceSizes = {
  mobileS: '320px',
  mobileM: '375px',
  mobileL: '450px',
  tablet: '768px',
  tabletL: '1024px',
};
const devices = {
  mobileS: `only screen and (max-width: ${deviceSizes.mobileS})`,
  mobileM: `only screen and (max-width: ${deviceSizes.mobileM})`,
  mobileL: `only screen and (max-width: ${deviceSizes.mobileL})`,
  tablet: `only screen and (max-width: ${deviceSizes.tablet})`,
  tabletL: `only screen and (max-width: ${deviceSizes.tabletL})`,
};
const calcRemFromPx = (size) => `${size / 16}rem`;
const intervalsInRem = {
  small: calcRemFromPx(20),
  middle: calcRemFromPx(40),
  large: calcRemFromPx(60),
  xlarge: calcRemFromPx(80),
};
const marginsInRem = {
  small: calcRemFromPx(5),
  middle: calcRemFromPx(10),
  large: calcRemFromPx(15),
  xlarge: calcRemFromPx(20),
};
const paddingsInRem = {
  small: calcRemFromPx(10),
  middle: calcRemFromPx(20),
  large: calcRemFromPx(40),
  xlarge: calcRemFromPx(60),
};
const fontsizesInRem = {
  small: calcRemFromPx(10),
  middle: calcRemFromPx(15),
  large: calcRemFromPx(20),
  xlarge: calcRemFromPx(30),
};
const fontWeights = {
  thin: 50,
  normal: 100,
  bold: 300,
};
const colors = {
  base: 'olive',
  softedBase: 'seagreen',
  weightedBase: 'darkgreen',
  invertedBase: 'rgba(255, 255, 255, 0.9)',
  warn: 'red',
  normal: 'black',
};
// margin : outside
// padding: inside
// justify-comtent : children Element 사이 여백을 최대로
// display: inline, flex, block, none
const align = {
  blocked: `
    display: block;
    clear: both;
    width: 100%;
  `,
  floated: `
    display: flex;
    float: left;
  `,
};
const types = {
  container: `
  margin: 0 auto;
  padding: 0;
  box-sizing: border-box;
  justify-content: space-between;
`,
  card: `
  align-items: center;
  margin: ${marginsInRem.middle};
  padding: ${paddingsInRem.middle};
  box-sizing: border-box;
  color: ${colors.narmal};
  background: ${colors.softedBase};
  border-radius: ${fontsizesInRem.small};
  `,
  button: `
  align-items: center;
  justify-content: center;
  text-align: center;
  box-sizing: border-box;
  margin: ${marginsInRem.small};
  padding: ${paddingsInRem.small};
  border: ${marginsInRem.small} solid ${colors.softedBase};
  border-radius: ${fontsizesInRem.large};
  font-size: ${fontsizesInRem.middle};
  font-weight: ${fontWeights.normal};
  color: ${colors.invertedBase};
  background: ${colors.weightedBase};
  &:hover {
    color: ${colors.narmal};
    background: ${colors.narmal};
    cursor: pointer;
  }
`,
  logo: `
  margin: ${marginsInRem.small};
  padding: ${paddingsInRem.small};
  box-sizing: border-box;
  border: none;
  text-align: center;
  font-size: ${fontsizesInRem.xlarge};
  font-weight: ${fontWeights.normal};
  color: ${colors.invertedBase};
  background: none;
`,
};
const globalTheme = {
  types,
  colors,
  align,
  devices,
  deviceSizes,
  intervalsInRem,
  marginsInRem,
  paddingsInRem,
  fontsizesInRem,
  fontWeights,
};
export default globalTheme;
